* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  .counter-container {
    display: flex;
  }
  
  .counter {
    font-weight: bold;
    margin: 1em auto;
  }
  
  .count {
    font-size: 8em;
    text-align: center;
  }
  
  .controls {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  
  button:hover {
    color: white;
    background-color: black;
  }
  

